/* styles.css */

.title-modal {
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    text-align: center;
  }
  
  .o-botones {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    position: relative;
    padding-bottom: 5rem;
    z-index: 1;
  }
  
  .btn-modal {
    flex-grow: 1;
    background-color: #fe6600;
    max-width: 270px;
    border: none;
    border-radius: 8px;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    margin: 10px 0;
    transition: background-color 0.3s ease; /* Transición de color al pasar el mouse */
  }
  
  .btn-modal:hover {
    background-color: #cf5504;
  }





  .modal-shap {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 150px;
    height: 150px;
    background: rgba(0, 0, 0, .3);
    clip-path: polygon(100% 0, 100% 100%, 0 100%);
}

.modal-shap:after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 125px;
    height: 125px;
    background: #292929;
    clip-path: polygon(100% 0, 100% 100%, 0 100%);
}