:root {
    --heading-color: #2d2d2d;
    --primary-color: #fe6600;
    --primary-color-rgb: 254,102,0;
}

body {
    color: #747474;
    font-size: 15px;
    font-weight: 400;
    line-height: 28px;
    font-family: 'Roboto', sans-serif;
}

a {
    color: #747474;
    cursor: pointer;
    outline: none;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    text-decoration: none;
}

a:hover,
a:focus,
a:visited {
    outline: none;
    color: var(--primary-color, #fe6600);
    text-decoration: none;
}


h2
 {
    color: var(--heading-color, #2d2d2d);
    line-height: 1.1;
    font-family: 'Roboto', sans-serif;
}



h2 {
    font-size: 30px;
}

.btn-login {
    background-color: #fe6600; /* Color de fondo naranja */
    color: white; /* Color del texto blanco */
    border: none; /* Sin borde */
    border-radius: 10px; /* Bordes redondeados */
    padding: 10px 20px; /* Espaciado interior del botón */
    cursor: pointer; /* Cambia el cursor al pasar por encima */
    text-decoration: none; /* Elimina la decoración de texto por defecto */
    font-size: 16px; /* Tamaño del texto */
  }
  
  .btn-login:hover {
    background-color: #cf5504; /* Cambia el color de fondo al pasar el mouse */
  }

/** Section Title style */
