.container-images {
    position: relative;
    max-width: 300px;
    padding: 50px;
    width: 100%;
    min-height: 500px;
    z-index: 1;

  }

  /* Estilos para la primera imagen (superior izquierda) */
  .image-sobre-1 {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 60%;
    height: 60%; 
    min-height: 200px; 
    object-fit: cover; /* Ajusta la imagen para llenar su contenedor */
  }

  /* Estilos para la segunda imagen (inferior derecha) */
  .image-sobre-2 {
    position: absolute;
    bottom: 0;
    right: 0;
    max-width: 60%; 
    height: 60%; 
    min-height: 200px; 
    object-fit: cover; /* Ajusta la imagen para llenar su contenedor */
  }

