}

/* CardContainer.css */

.card-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px; /* Espacio entre las tarjetas */
  }
  
  .card-item {
    /* flex: 1; */ /* Hace que todas las tarjetas tengan el mismo ancho */
    width: 300px; /* Ancho mínimo para cada tarjeta */
    /* min-width: 200px; */
    max-width: 80%;
  }
  
  /* Asegúrate de que las tarjetas tengan la misma altura */
  .card-item > div {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .copyright-section{
    
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    box-sizing: border-box;
    min-height: 5rem;
    background: #333333;
    color: #f7f9fb;
}

.copyright-section p span{
    color: #fe6600
}

.copyright-section div{
    flex: 1;
    display: flex;
    
}

@media (max-width: 600px) {
    .center-container-color {
      flex-direction: column; /* Cambia a disposición de columna */
      align-items: stretch; /* Estirar los contenedores para llenar el ancho */
    }
    .o-col{
        width: 90%;
    }

    .services-section .row{
        margin-bottom: 10rem;
    }
  }

  @media (max-width: 1023px) {
    .section-1{
        flex-direction: column;
        
    }

    .section-1-reverse{
        flex-direction: column;
    }

    .section-1 .sub-section{
        /* flex: 1; */
        width: 90%;

    }
  }

@media only screen and (min-width: 600px) {}

.section-detail {
    position: relative;
}

.section-detail:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 300px;
    height: 300px;
    background: rgba(254, 102, 0, .15);
    clip-path: polygon(100% 0, 0 100%, 0 0);
}

.section-detail:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 265px;
    height: 265px;
    background: var(--primary-color, #fe6600);
    clip-path: polygon(100% 0, 0 100%, 0 0);
}

