/** Button style */
.theme-btn,
a.theme-btn {
    color: white;
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    min-width: 230px;
    transition: 0.5s;
    padding: 20px 41px;
    border-radius: 15px;
    background: var(--primary-color, #fe6600);
    display: inline-block;
}

.theme-btn i {
    font-size: 11px;
    padding-left: 10px;
    transition: 0.5s;
}

.theme-btn:hover i {
    transform: translateX(5px);
}

.form-group input,
.form-group textarea {
    border: none;
    padding: 17px 30px;
    color: #FFFFFF;
    border-radius: 15px;
    margin-bottom: 20px;
    background: #FF8431;
}

.form-group input::placeholder,
.form-group textarea::placeholder {
    color: #FFF6F0;
}

.form-group input:focus,
.form-group textarea:focus {
    color: #FFFFFF;
    box-shadow: none;
    background: #FF8431;
}

/* input buttons */
/* button:focus, */
input[type="button"]:focus,
input[type="reset"]:focus,
input[type="submit"]:focus {
    outline: none;
}

.overlay {
    position: relative;
}

.overlay:before {
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    opacity: 0.1;
    background: #000000;
}

