/**************** 05. About Us Section  *************** */
/****************************************************** */
.about-image {
    max-width: 500px;
    padding: 50px;
    position: relative;
}

.about-image:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 192px;
    height: 192px;
    border-radius: 50%;
    /* z-index: -1; */
    background: var(--primary-color, #fe6600);
}

.about-image:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 191px;
    height: 191px;
    /* z-index: -1; */
    background: #292929;
    clip-path: polygon(100% 100%, 0 100%, 0 0);
}

.about-image img {
    border-radius: 100px 0 0 0;
    clip-path: polygon(100% 0, 100% 100%, 110px 100%, 0 calc(100% - 110px), 0 0);
    max-width: 100%;
    min-height: 507px;
    object-fit: cover;
    object-position: right; /* revert */
   /*  width: 400px;
    height: 507px;
    object-fit: cover;
    object-position: revert; */
}

/*Custom Animation*/
@-webkit-keyframes fadeInBottomLeft {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(-100px, 100px, 0);
        transform: translate3d(-100px, 100px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
}
@keyframes fadeInBottomLeft {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(-100px, 100px, 0);
        transform: translate3d(-100px, 100px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
}
.fadeInBottomLeft {
    -webkit-animation-name: fadeInBottomLeft;
    animation-name: fadeInBottomLeft;
}

.about-border {
